import { useState } from 'react';
import styled from 'styled-components';
import JSZip from 'jszip';
import { saveAs } from 'file-saver';
import Pako from 'pako';

const Container = styled.div`
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #1e1e1e;
`;

const TextS = styled.textarea`
  position: relative;
  width: 80vw;
  height: 50vh;
  text-align: initial;
  background-color: #787878;
  ::placeholder,
  ::-webkit-input-placeholder {
    color: black;
    opacity: 0.7;
  }
`;

const TextT = styled.input`
  position: relative;
  width: 10rem;
  height: 1rem;
  text-align: center;
  margin: 1rem;
  background-color: #787878;
  ::placeholder,
  ::-webkit-input-placeholder {
    color: black;
    opacity: 0.7;
  }
`;

const Btn = styled.button`
  position: relative;
  margin: 50px;
  width: 200px;
  height: 50px;
  border-radius: 0.4rem;
  border-color: transparent;
  background-color: #cfcfcf;
`;

const initialValues = {
  text: '',
  tag: '',
};

function App() {
  // const [text, setText] = useState('');
  // const [tag, setTag] = useState('');
  const [values, setValues] = useState(initialValues);

  const decode = (b) => {
    const b64Chars =
      'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/';
    let b64CharMap = [];
    for (let i = 1; i <= 64; i++) {
      b64CharMap[b64Chars.charCodeAt(i - 1)] = i - 1;
    }

    let cmap = b64CharMap;
    let e1, e2, e3, e4;
    let st = [];
    let t3 = [];
    if (b.match('[^0-9A-Za-z/+=]')) {
      alert('invalid string');
      return false;
    }
    for (let i = 1; i <= b.length; i += 4) {
      e1 = cmap[b.charCodeAt(i - 1)];
      e2 = cmap[b.charCodeAt(i)];

      if (e1 === undefined || e2 === undefined) {
        alert('invalid length');
        return false;
      }
      e3 = cmap[b.charCodeAt(i + 1)];
      e4 = cmap[b.charCodeAt(i + 2)];
      t3[1] = String.fromCharCode((e1 << 2) | (e2 >> 4));
      if (e3 === undefined) {
        t3[2] = null;
        t3[3] = null;
        st[st.length + 1] = t3.join('');
        break;
      }
      t3[2] = String.fromCharCode((e2 << 4) | ((e3 >> 2) & 0xff));
      if (e4 === undefined) {
        t3[3] = null;
        st[st.length + 1] = t3.join('');
        break;
      }
      t3[3] = String.fromCharCode((e3 << 6) | (e4 & 0xff));
      st[st.length + 1] = t3.join('');
    }
    return st.join('');
  };

  const handleTag = (tag) => {
    let base = tag.toLowerCase();
    let k = base.length;
    if (k > 16) {
      base = base.substring(k - 16, k);
      k = 16;
    }
    k = 16 - k;
    let buffer = [];
    for (let i = 0; i <= 15; i++) {
      let j = (i - k) & 0xf;
      buffer[i] = base.charCodeAt(j);
    }
    for (let i = 0; i <= 15; i++) {
      buffer[-i & 0xf] =
        buffer[(15 - i) & 0xf] ^
        buffer[(13 - i) & 0xf] ^
        buffer[(12 - i) & 0xf] ^
        buffer[(10 - i) & 0xf];
    }
    let buffer1 = [];
    let j = 0;
    base = buffer;

    for (let i = 0; i <= 255; i++) {
      buffer1[i] = i;
    }
    for (let i = 0; i <= 255; i++) {
      j = (j + buffer1[i] + base[i & 0xf]) & 0xff;
      let temp = buffer1[i];
      buffer1[i] = buffer1[j];
      buffer1[j] = temp;
    }
    return buffer1;
  };

  const formatString = () => {
    //get the guides part of the string.
    let str = values.text.match(/[\w+=/]+/g)[2];

    //string decoder.
    let base = values.tag;
    let s = handleTag(base); //generate scrambled charCode array of battletag.
    let strData = decode(str); //b64 decode string.
    let buffer = [];
    let i = 0;
    let j = 0;
    for (let k = 0; k < strData.length; k++) {
      i = (i + 1) & 0xff;
      j = (j + s[i]) & 0xff;
      let temp = s[i];
      s[i] = s[j];
      s[j] = temp;
      buffer.push(
        String.fromCharCode(strData.charCodeAt(k) ^ s[(s[i] + s[j]) & 0xff]) //more scrambling and back to array of string.
      );
    }
    let charData = buffer.map(function (x) {
      //back again to charCode array, not sure if these two steps can be merged but will leave like this for now.
      return x.charCodeAt(0);
    });

    let binData = new Uint8Array(charData);

    let data = Pako.inflate(binData); //pako zlib decompress the charCode array.

    let final = [];
    for (let i = 0; i < data.length; i++) {
      final[i] = String.fromCharCode(data[i]);
    }
    let finalStr = final.join('');

    let finalArr = finalStr.split(/\0/).filter((e) => e.length > 0); //readable string of guides.

    //format the string to generate guide files.
    let allianceArr = [];
    let hordeArr = [];
    let customArr = [];
    for (let i = 0; i < finalArr.length; i++) {
      const groupIndex = finalArr[i].indexOf('#group ') + 16;
      const nameIndex = finalArr[i].indexOf('#name ') + 6;
      const level = finalArr[i][nameIndex] + finalArr[i][nameIndex + 1];
      const endNameIndex = finalArr[i].indexOf('\n', groupIndex);
      const group = finalArr[i].substring(groupIndex, endNameIndex);
      let currArr;
      if (group.search('Alliance') >= 0) {
        currArr = allianceArr;
      } else if (group.search('Horde') >= 0) {
        currArr = hordeArr;
      } else {
        currArr = customArr;
      }
      currArr.push([
        `RXPGuides.RegisterGuide("${group}",[[` + finalArr[i] + ']])\n',
        level,
      ]);
    }
    if (allianceArr.length > 0) {
      allianceArr.sort((a, b) => a[1] - b[1]);
    }
    if (hordeArr.length > 0) {
      hordeArr.sort((a, b) => a[1] - b[1]);
    }
    return [allianceArr, hordeArr, customArr];
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setValues({ ...values, [name]: value });
  };

  const handleClick = () => {
    if (values.text === '' || values.tag === '') {
      return;
    }
    //decode and format.
    let [allianceArr, hordeArr, customArr] = formatString();

    //generate files.
    const zip = new JSZip();
    if (allianceArr.length > 0) {
      zip.file('Alliance.lua', allianceArr.map((a) => a[0]).join(''));
    }
    if (hordeArr.length > 0) {
      zip.file('Horde.lua', hordeArr.map((a) => a[0]).join(''));
    }
    if (customArr.length > 0) {
      zip.file('Custom.lua', hordeArr.map((a) => a[0]).join(''));
    }
    zip.generateAsync({ type: 'blob' }).then(function (content) {
      saveAs(content, 'guides.zip');
    });
  };

  return (
    <Container>
      <TextT
        onChange={handleChange}
        name="tag"
        type="text"
        placeholder="johndoe#1234"
        value={values.tag}
      ></TextT>
      <TextS
        onChange={handleChange}
        name="text"
        placeholder="paste your string here"
        rows="1"
        cols="50"
        value={values.text}
      ></TextS>

      <Btn onClick={handleClick}>Convert String</Btn>
    </Container>
  );
}

export default App;
